//å°è£…ä¸€ä¸‹è¿™ä¸ªajaxè¯·æ±‚
const ajax = (url, fn) => {
    const xhr = new XMLHttpRequest()//1.åˆ›å»ºå¯¹è±¡
    xhr.open('get', url)//å‘é€è¯·æ±‚
    //ç›‘å¬onreadystateChageäº‹ä»¶
    let result = ''
    xhr.onreadystatechange = () => {

        if (xhr.readyState === 4 && xhr.status === 200) {
            fn.call('', xhr.responseText)
        }
    }
    xhr.send()
    return result
}

